@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
body,
html {
  margin: 0;
  font-family: "Noto Sans", sans-serif, Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input,
textarea,
button {
  outline: none;
  border: none;
  font-family: "Noto Sans", sans-serif, Arial, Helvetica, sans-serif;
}

.theme-dark {
  --primary-bg: rgb(5, 12, 7);
  --secondary-bg: rgb(0, 20, 0);
  --primary-bg1: rgb(4, 9, 83);
  --secondary-bg1: rgb(9, 50, 97);
  --tertiary-bg: rgb(177, 177, 177);
  --primary-clr: rgb(221, 221, 221);
  --input-bg: rgba(0, 0, 0, 0.4);
  --font-color: #fff;
}
.theme-light {
  --primary-bg: #fff;
  --secondary-bg: #fefefe;
  --primary-bg1: #008cff;
  --secondary-bg1: #fefefe;
  --tertiary-bg: #cecece;
  --primary-clr: #000;
  --input-bg: rgba(255, 255, 255, 0.4);
  --font-color: #000;
}
.primarybg {
  background-color: var(--primary-bg1);
}

.secondarybg {
  background-color: var(--secondary-bg1);
}

.tertiarybg {
  background-color: var(--tertiary-bg);
}

.primaryclr {
  color: var(--primary-clr);
}
.app_container {
  height: 100vh;
}

.flexCenter {
  display: flex;
  align-items: center;
  justify-content: center;
}
.marginTB10 {
  margin: 10px 0;
}

.container-flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}


.header-container{
    box-shadow: 0px 10px 44px 4px rgba(1, 79, 54, 0.2);
}

.header_logo{
   padding: 12px 0px;
   font-size: 2em;
   text-align: center;
}
.suggestionbox{
    width: 40px;
    height: 40px;
    position: absolute;
    border-radius: 4px;
    transition: 0.2s;
}
.textfield_container {
  margin: 100px auto 0px auto;
  width: 600px;
  max-width: 90vw;
  height: 300px;
  position: relative;
}
.textfield_container > textarea {
  background: var(--input-bg);
  resize: none;
  border: 2px solid rgb(0, 56, 56);
  color: var(--font-color);
  letter-spacing: 1px;
  font-size: 18px;
  width: 100%;
  height: 100%;
}

.app__switch {
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  border: 0;
  border-radius: 1em;
  width: 40px;
  height: 14px;
  cursor: pointer;
  background-color: var(--primary-bg);
}
.app__switch:checked {
  background-color: var(--primary-bg1);
}
.switchBtn {
  display: flex;
  align-items: center;
  position: relative;
}
.switchBtn label {
  margin-left: 10px;
  color: var(--font-color);
}
.switchBtn .switch__thumb {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 50%;
  left: 0;
  background-color: var(--tertiary-bg);
  border-radius: 50%;
  transition: all 0.1s;
  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.2);
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.switchBtn .switch__thumb--icon {
  width: 20px;
  height: 20px;
  font-size: 20px;
  line-height: 22px;
}
.switchBtn .app__switch:checked + .switch__thumb {
  -webkit-transform: translateX(calc(100% + 10px)) translateY(-50%);
          transform: translateX(calc(100% + 10px)) translateY(-50%);
  box-shadow: -1px 0 5px rgba(0, 0, 0, 0.2);
}

